import { createContext } from "react";
import { decorate, observable, computed } from "mobx";

class TransferData {
  listTitle = "";

  myData = [];

  list = [];
  suggestion = null;

  get data() {
    return this.list;
  }

  pseudo = (title) => {
    this.listTitle = title;
  };

  transfer = (data) => {
    this.myData = data;
  };

  download = (data) => {
    let newData = data.filter((item) => !item.suggested);

    newData.map((item) => {
      const datum = item.votes.find((item) => item.pseudo === this.listTitle);
      item.vote = datum ? datum.vote : 0;
    });

    this.list = newData;

    this.suggestion = data.find(
      (item) => item.suggested && item.pseudo === this.listTitle
    );
  };
}

decorate(TransferData, {
  list: observable,
  data: computed
});

const data = new TransferData();

export default createContext(data);
