import Rubrique from "../workshop/rubriques/Rubrique-1";

import React, { useState, useEffect, useContext } from "react";
import { observer } from "mobx-react-lite";

import Adhoc from "./adhoc";
import transferData from "./transferData";

const App = observer(() => {
  const dataContext = useContext(transferData);
  const [data, setData] = useState(dataContext);

  useEffect(() => {
    setData(dataContext);
    // eslint-disable-next-line
  }, [dataContext.list]);

  return (
    <div className="scroll" style={{ overflow: "auto", height: "100vh" }}>
      <Adhoc />
      <div className="App">
        <Rubrique data={data} />
      </div>
    </div>
  );
});

export default App;
