import React, { useState, useEffect, useContext } from "react";

import { gql } from "apollo-boost";
import {
  ApolloProvider,
  useQuery,
  useMutation,
  useSubscription
} from "@apollo/react-hooks";
import { ApolloClient } from "apollo-client";
import { InMemoryCache } from "apollo-cache-inmemory";
import { WebSocketLink } from "apollo-link-ws";
import { split } from "apollo-link";
import { HttpLink } from "apollo-link-http";
import { getMainDefinition } from "apollo-utilities";

import transferData from "./transferData";

const httpLink = new HttpLink({
  // uri: 'http://localhost:4000/graphql'
  uri: "https://adhoc-workshops.herokuapp.com/graphql"
});

const wsLink = new WebSocketLink({
  // uri: `ws://localhost:4000/graphql`,
  uri: `wss://adhoc-workshops.herokuapp.com/graphql`,
  options: {
    reconnect: true
  }
});

const link = split(
  ({ query }) => {
    const { kind, operation } = getMainDefinition(query);
    return kind === "OperationDefinition" && operation === "subscription";
  },
  wsLink,
  httpLink
);

const client = new ApolloClient({
  link: link,
  cache: new InMemoryCache()
});

const GET_CONTENT = gql`
  query {
    Place(id: "Wifilles-2021") {
      Contents {
        list
        description
        image
        votes {
          pseudo
          vote
        }
        pseudo
        suggested
      }
    }
  }
`;

const NEWPLACE_SUBSCRIPTION = gql`
  subscription {
    newPlace(placeId: "Wifilles-2021") {
      Contents {
        list
        description
        image
        votes {
          pseudo
          vote
        }
        pseudo
        suggested
      }
    }
  }
`;

const ADD_CONTENT = gql`
  mutation AddContent(
    $list: String!
    $pseudo: String!
    $description: String!
    $image: String!
    $vote: Float!
  ) {
    addContent(
      list: $list
      pseudo: $pseudo
      description: $description
      image: $image
      vote: $vote
      place_id: "Wifilles-2021"
    ) {
      list
    }
  }
`;

const DELETE_CONTENT = gql`
  mutation AddContent($list: String!) {
    deleteContent(list: $list, place_id: "Wifilles-2021") {
      list
    }
  }
`;

function Query() {
  const newData = useContext(transferData);

  const { data } = useQuery(GET_CONTENT);
  let contents = data?.Place?.Contents;

  // console.log("Data from Server Query", data);

  useEffect(() => {
    if (contents) newData.download(contents);
    // eslint-disable-next-line
  }, [contents]);

  return <></>;
}

function Subscription() {
  const newData = useContext(transferData);
  const { data } = useSubscription(NEWPLACE_SUBSCRIPTION);

  // console.log("Data from Server Subscription", data);

  let contents = data?.newPlace?.Contents;

  useEffect(() => {
    if (contents) newData.download(contents);
    // eslint-disable-next-line
  }, [contents]);

  return <></>;
}

function Mutation() {
  const newData = useContext(transferData);

  const [listTitle, setListTitle] = useState();

  const [addContent] = useMutation(ADD_CONTENT);
  const [deleteContent] = useMutation(DELETE_CONTENT);

  useEffect(() => {
    setListTitle(newData.listTitle);
  }, [newData.listTitle]);

  function addData() {
    if (!newData.myData.length) {
      alert("Merci de transférer d'abord des Données !");
    } else {
      const checkData = newData.myData.filter(
        (item) => item.description?.length && item.image?.length
      );

      if (newData?.myData?.length === checkData.length) {
        newData.myData.forEach((item, i) =>
          addContent({
            variables: {
              list: item.list ? item.list : listTitle,
              pseudo: listTitle,
              description: item.description,
              image: item.image,
              vote: item.vote ? item.vote : 0
            }
          })
        );
      } else {
        alert(
          'Merci de vérifier si les Données contiennent bien : "description" et "image" !'
        );
      }
    }
  }

  function deleteData() {
    if (!listTitle) {
      alert("Merci d'entrer d'abord votre ListTitle !");
    } else {
      deleteContent({
        variables: {
          list: listTitle
        }
      });
    }
  }

  return (
    <div className="navbar">
      {listTitle && (
        <>
          <h4
            style={{
              color: "#5F9EA0",
              background: "#FFF",
              minWidth: 200,
              borderRadius: 5,
              padding: 4,
              marginRight: 12
            }}
          >
            {listTitle}
          </h4>
          <button className="send" onClick={addData}>
            Envoyer
          </button>
          <button className="delete" onClick={deleteData}>
            Effacer
          </button>
        </>
      )}
    </div>
  );
}

function Adhoc() {
  return (
    <ApolloProvider client={client}>
      <Query />
      <Subscription />
      <Mutation />
    </ApolloProvider>
  );
}

export default Adhoc;
